.dropbtn {
  background-color: #000;
  color: white;
  font-size: 16px;
  line-height: 19px;
  border: none;
  font-family: "Roboto", sans-serif;
  font-weight: bold;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown_content {
  display: none;
  position: absolute;
  background-color: #000;
  min-width: 180px;
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown_content li {
  padding: 5px;
}

.dropdown:hover .dropdown_content {
  display: block;
}

.dropdown_content li:hover {
  background-color: #ddd;
  color: #000;
}