.content {
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: rgb(56, 56, 56);
  font-family: "Roboto", sans-serif;
  font-size: 20px;
}

.wrapper {
  width: 1200px;
  margin: 0 auto;
}

.title > h3 {
  text-align: center;
  font-size: 35px;
  margin-bottom: 50px;
}

.title:last-child {
  margin-top: 50px;
}

.info > p {
  text-align: center;
}

.description_important {
  font-weight: bold;
  text-decoration: underline;
  text-align: center;
}

.leagues_item {
  padding-top: 20px;
}

.staf li{
  list-style-type: square;
}

.staf_item {
  padding-top: 10px;
}
